@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Home {
    text-align: center;
  }
  
  .full-screen{
    height: 100vh;
  }
  
  .Home-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .Home-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .Home-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .Home-link {
    color: #61dafb;
  }
  
  .player-wrapper {
    position: relative;
    padding-top: 56.25% /* Player ratio: 100 / (1280 / 720) */
  }
  
  .react-player {
    position: absolute;
    top: 0;
    left: 0;
  }
  
  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
.Aline{
    text-align: left;
}


.master-image{
    width: 100%;
}

.container{
    width: 90%;
    height: 70%;
    max-width:1440;
    margin: 100px auto;
}


.black-bg{
    background-color: #2e2e2e;
    padding-top: 60px;
    padding-bottom: 60px;
}

.root {
    display: -webkit-flex;
    display: flex;
    offset: 60;
    max-width: 1440;
    grid-template-columns: 1fr;
}

.text-title{
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
}



.text-explanation{
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 150%;
}

.text-body{
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    margin-top: 20px;
}

.text-subtitle{
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
}

.text-subtitle-white{
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color : #FFFFFF;
}

.text-phrase{
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 54px; 
    text-align: center;
    color: #FFFFFF;
    
}

.text-phrase-explanation{
    font-family: Poppins;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    color: #FFFFFF;
    padding-bottom: 15px;
}

a {
    color:black;
  }

.player-wrapper {
    position: relative;
    padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
    width: 90%;
    max-width:1440;
    margin: 100px auto;
}
  
.react-player {
    position: absolute;
    top: 0;
    left: 0;
  }

.background{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
